-style: none;\n  color: black;\n}\n\n.commentSubmit {\n  width: 5rem;\n  height: 2.5rem;\n  border-radius: 5px;\n  padding-left: 0.5rem;\n  align-self: flex-start;\n  background-color: rgb(14, 185, 51);\n  font-size: 1rem;\n  text-decoration: none;\n  font-weight: bold;\n  border-width: 2px;\n}\n\n/* TEST CSS */\n\n.popup {\n  width: 100%;\n  background-color: rgba(32, 32, 32, 0.9);\n  display: none;\n  justify-content: center;\n  align-items: center;\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  z-index: 10;\n}\n\n .popup_section {\n  height: 90%;\n  width: 90%;\n  max-width: 48rem;\n  background-color: #fff;\n  border-radius: 5px;\n  background-repeat: no-repeat;\n  background-position: top right;\n  background-clip: border-box;\n  background-size: cover;\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  justify-content: flex-start;\n  align-items: flex-start;\n}\n\n .popbtn {\n  width: 2rem;\n  height: 2rem;\n  border-radius: 50%;\n  background-color: transparent;\n  border: 1px solid;\n  transition: background-color ease-out 250ms;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  position: absolute;\n  top: 0.5rem;\n  right: 0.5rem;\n  z-index: 11;\n}\n\n.closepop {\n  width: 100%;\n  height: 100%;\n  margin: 0;\n  padding: 0;\n  border: 0;\n  outline: 0;\n  background-color: transparent;\n  cursor: pointer;\n}\n\n.popbtn:hover {\n  background-color: grey;\n}\n\n.popbtn:hover .closepop {\n  color: #fff;\n}\n\n.popup_movies {\n  width: 100%;\n  height: 100%;\n  padding: 1rem;\n  overflow-y: auto;\n  background-color: rgba(255, 255, 255, 0.8);\n  position: relative;\n  scrollbar-width: thin;\n  scrollbar-color: grey;\n}\n\n.popup_movies  {\n  width: 100%;\n  margin-bottom: 2rem;\n}\n\n.popup_movies \n:last-child {\n  margin-bottom: 0;\n}\n\n.popup_movies .h2 {\n  margin-bottom: 2rem;\n}\n\n.popup_movies .h3 {\n  margin-bottom: 1rem;\n}\n\n.popup_movies .ul {\n  list-style: none;\n}\n\n.popup_movies .ul .li {\n  display: flex;\n  flex-direction: row;\n  justify-content: flex-start;\n  align-items: flex-start;\n  flex-wrap: wrap;\n  padding: 0.5rem 0;\n}\n\n.btn {\n  padding: 0.25rem 0.5rem;\n  background: transparent;\n  border: 1px solid;\n  cursor: pointer;\n  background-color: transparent;\n  transition: background-color ease-out 250ms;\n}\n\n.btn:hover {\n  border: 1px solid;\n  background-color: grey;\n  color: #fff;\n}\n\n/*.popup .popup_movies::-webkit-scrollbar-thumb {\n  background-color: grey;\n  border-radius: 20px;\n}\n.popup .popup_movies::-webkit-scrollbar {\n  width: 6px;\n}\n.popup .popup_movies::-webkit-scrollbar-track {\n  background: grey; \n}*/\n\n.movie__details {\n  width: auto;\n}\n\n.img {\n  width: 210px;\n  height: auto;\n  margin-right: 1rem;\n  margin-bottom: 1rem;\n  float: left;\n}\n\n.movie__more {\n  margin: 0;\n  width: auto;\n  margin-bottom: 1rem;\n}\n\n.item-category {\n  margin-right: 0.5rem;\n  font-weight: 600;\n}\n\n/*.popup .movie__comments, .popup .movie__reservations {\n  clear: both;\n}\n.popup .movie__comments .item-date, .popup .movie__reservations .item-date, .popup .movie__comments .item-name, .popup .movie__reservations .item-name {\n  margin-right: 0.5rem;\n}\n.popup .movie__comments .item-name, .popup .movie__reservations .item-name {\n  font-weight: 600;\n}\n.popup .movie__comments .item-date, .popup .movie__reservations .item-date {\n  font-weight: 400;\n}*/\n\n.popup .movie__add-comment {\n color: #000;\n}\n\n.form {\n  width: 100%;\n  max-width: 31.25rem;\n  padding-top: 0.5rem;\n  display: flex;\n  flex-direction: column;\n  justify-content: flex-start;\n  align-items: flex-start;\n  margin-bottom: 0.75rem;\n}\n\n.movie__add-comment .form \n:last-child {\n  margin-bottom: 0;\n}\n\n.input__field {\n  width: 100%;\n  padding: 0.5rem;\n  border: none;\n  background-color: transparent;\n  outline: 1px solid grey;\n}\n\n.input__field:focus {\n  outline: 1px solid;\n}\n\n.form__message {\n  display: none;\